<template>
  <div>
    <div class="tab-header">
      <h2>Portworx Details</h2>
    </div>
    <div
      v-if="isInitDataFetching"
      style="display: flex; justify-content: center; align-items: center; height: 60vh;"
    >
      Loading...
    </div>
    <div v-else-if="!isInitDataFetching && pageError !== 'none'" id="banner-px-not-installed" style="margin-top: 1rem;">
      <Banner
        v-if="pageError === 'px-not-installed'"
        class="type-banner mb-20 mt-0"
        color="warning"
      >
        Portworx is not installed on this cluster. Click
        <a style="cursor: pointer;" :href="EXTERNAL_LINKS.INSTALL_PX" target="_blank">here</a>
        to learn how to install Portworx.
      </Banner>
      <Banner
        v-else-if="pageError === 'failed-fetching-services'"
        class="type-banner mb-20 mt-0"
        color="error"
      >
        Unable to fetch services for the cluster. Please try again later.
      </Banner>
      <Banner
        v-else-if="pageError === 'px-secure-cluster'"
        class="type-banner mb-20 mt-0"
        color="warning"
      >
        Portworx extension is currently not supported for PX-Security enabled clusters.
      </Banner>
      <Banner
        v-else-if="pageError === 'failed-fetching-cluster'"
        class="type-banner mb-20 mt-0"
        color="error"
      >
        Unable to retrieve storage cluster details. Please try again later.
      </Banner>
    </div>
    <div v-else>
      <div style="display: flex; margin-top: 1.5rem;">
        <div id="pvc-details-left" style="display: flex; flex-direction: column; width: 50%; padding-right: 15px;">
          <div style="margin-bottom: 1.5rem;">
            <h4>Name</h4>
            <h5>{{ selfPvc?.metadata?.name || '-' }}</h5>
          </div>
          <div style="margin-bottom: 1.5rem;">
            <h4>Status</h4>
            <h5>{{ selfPvc?.status?.phase || '-' }}</h5>
          </div>
          <div style="margin-bottom: 1.5rem;">
            <h4>Replication Factor</h4>
            <h5>{{ volume?.labels?.repl || '-' }}</h5>
          </div>
          <div style="margin-bottom: 1.5rem;">
            <h4>Labels</h4>
            <div v-if="labels?.length" style="display: flex; flex-wrap: wrap; gap: 5px;">
              <div v-for="(lbl, index) in labels" :key="index">
                <BadgeState color="bg-info" :label="lbl"></BadgeState>
              </div>
            </div>
            <h5 v-else>
              -
            </h5>
          </div>
        </div>

        <div id="pvc-details-left" style="display: flex; flex-direction: column; width: 50%; padding-left: 15px;">
          <div style="margin-bottom: 1.5rem;">
            <h4>Size</h4>
            <h5>{{ selfPvc?.spec?.resources?.requests?.storage || '-' }}</h5>
          </div>
          <div style="margin-bottom: 1.5rem;">
            <h4>Format</h4>
            <h5>{{ volume?.volume?.format?.split('_')[2] || '-' }}</h5>
          </div>
          <div style="margin-bottom: 1.5rem;">
            <h4>Replicas on Nodes</h4>
            <h5>
              <div v-if="replicaNodes?.length > 0">
                <div v-for="(rep, index) in replicaNodes" :key="index">
                  Node: {{ rep?.data_ip || '-' }}
                </div>
              </div>
              <div v-else>
                -
              </div>
            </h5>
          </div>
          <div style="margin-bottom: 1.5rem;">
            <h4>Attached Node</h4>
            <h5>{{ volume?.volume?.attached_on || '-' }}</h5>
          </div>
          <div style="margin-bottom: 1.5rem;">
            <h4>Storage Class</h4>
            <div>
              <h5 v-if="!selfPvc?.spec?.storageClassName || selfPvc?.spec?.storageClassName === 'Use the default class'">{{ selfPvc?.spec?.storageClassName || '-' }}</h5>
              <router-link v-else :to="getStorageClassLink(selfPvc?.spec?.storageClassName)">
                {{ selfPvc?.spec?.storageClassName }}
              </router-link>
            </div>
          </div>
        </div>
      </div>
      <div>
        <h3>Volume Consumers</h3>
        <div>
          <div v-if="volume?.volume?.volume_consumers?.length" style="max-height:300px; overflow-y: auto; ">
            <ResourceTable
              data-testid="volume-consumers-sortable-table"
              :rows="volume.volume.volume_consumers"
              :headers="CONSUMERS_HEADERS"
              :table-actions="false"
              :row-actions="false"
              :search="false"
            >
              <template #col:name="{row}">
                <td>
                  <span>{{ row.name?.split('(')[0] || '-' }}</span>
                </td>
              </template>

              <template #col:namespace="{row}">
                <td>
                  <span>{{ row.namespace || '-' }}</span>
                </td>
              </template>

              <template #col:runningOn="{row}">
                <td>
                  <span>{{ row?.node_id || '' }}</span>
                </td>
              </template>

              <template #col:controlledBy="{row}">
                <td>
                  <span>{{ row?.owner_name }}</span>
                </td>
              </template>
            </ResourceTable>
          </div>
          <div v-else>
            <Banner
              class="type-banner mb-20 mt-0"
              color="info"
              :label="isInitDataFetching ? 'Loading volume consumers...' : 'No volume consumers found.'"
            />
          </div>
        </div>
      </div>
    </div>
  </div>
</template>

<script>

import BadgeState from '@components/BadgeState/BadgeState';
import { Banner } from '@components/Banner';
import ResourceTable from '@shell/components/ResourceTable';
import { EXTERNAL_LINKS } from '../../constants';
import { API_ENDPOINTS } from '../../api-endpoint';
import utilService from '../../services/util-service';
import { CONSUMERS_HEADERS } from './table-headers';

export default {
  name:           'PVCPortworxDetails',
  components: {
    BadgeState,
    Banner,
    ResourceTable
  },
  data() {
    return {
      isInitDataFetching:       true,
      pageError:                'none',
      pxInstalledNS:            '',
      selfPvc:                  {},
      volume:                   {},
      replicaNodes:             [],
      labels:                   [],
      pxSecureAuthToken:        '',
      cluster:                  {},
      CONSUMERS_HEADERS,
      EXTERNAL_LINKS
    };
  },
  methods: {
    async fetchData() {
      this.EXTERNAL_LINKS = EXTERNAL_LINKS;
      this.isInitDataFetching = true;
      this.selfPvc = this.$attrs.resource;
      try {
        const CLUSTER_NAME = this.$route.params.cluster;

        // Fetch the namespace on which Portworx is installed.
        [this.pxInstalledNS, this.pageError] = await utilService.methods.getPortworxNamespace(CLUSTER_NAME);
        if (this.pageError !== 'none') {
          // If Portworx is not installed or init data fetch failed, then do not proceed further.
          this.isInitDataFetching = false;

          return;
        }

        // Get the storage Cluster details. If the cluster is PX Secure cluster, then we will get pxSecureAuthToken
        // which will be used for further API calls.
        [this.cluster, this.pxSecureAuthToken, this.pageError] =
          await utilService.methods.getStorageClusterDetails(CLUSTER_NAME, this.pxInstalledNS);
        if (this.pageError !== 'none') {
          // If any error occurred while fetching the storage cluster details, then do not proceed further.
          this.isInitDataFetching = false;

          return;
        }

        const pxApiConfig = {};

        // For PX Secure cluster, we will have pxSecureAuthToken token.
        // In this case, for API calls made to portworx-api, we need to set the Authorization header.
        if (this.pxSecureAuthToken) {
          pxApiConfig.headers = { Authorization: `Bearer ${ this.pxSecureAuthToken }` };
        }

        const volumeRes = await this.$axios.post(API_ENDPOINTS.pxFilterVolumes(this.$route.params.cluster, this.pxInstalledNS), {
          name:    this.selfPvc?.spec?.volumeName,
          options: { deep: true },
        }, pxApiConfig).then(res => res).catch((error) => {
          if (error.response.status === 403) {
            this.pageError = 'px-secure-cluster';
          }

          return {};
        });

        this.volume = (volumeRes?.data?.volumes || [])[0];
        this.getLabels();

        if (volumeRes?.data?.volumes && this.volume?.volume?.replica_sets) {
          const nodeIds = [];

          this.volume.volume.replica_sets.forEach((replicaSet) => {
            nodeIds.push(...(replicaSet?.nodes || []));
          });
          this.replicaNodes = await this.getNodeSummary(nodeIds, pxApiConfig);
        }

        this.isInitDataFetching = false;
      } catch (error) {
        this.isInitDataFetching = false;
      }
    },
    async getNodeSummary(nodes, pxApiConfig) {
      try {
        const res = await Promise.allSettled(
          nodes.map(async(node) => {
            const res = await this.$axios.get(API_ENDPOINTS.pxNodeInspectApi(this.$route.params.cluster, node, this.pxInstalledNS), pxApiConfig);

            return res.data.node;
          }),
        ).then(res => res).catch((error) => {
          if (error.response.status === 403) {
            this.pageError = 'px-secure-cluster';
          }

          return {};
        });

        return res
          .filter(item => item.status === 'fulfilled')
          .map(x => (x).value);
      } catch (ex) {
        console.error('Error in getting nodes');
        throw ex;
      }
    },
    getStorageClassLink(scName) {
      return `/c/${ this.$route.params.cluster }/explorer/storage.k8s.io.storageclass/${ scName }#parameters`;
    },
    getLabels() {
      this.labels = [];

      if (this.volume?.labels) {
        Object.keys(this.volume.labels).forEach((key) => {
          this.labels.push(`${ key }: ${ this.volume.labels[key] }`);
        });
      }
    }
  },
  created() {
    this.fetchData();
  },
};
</script>

<style scoped>
H5 {
  opacity: 0.8;
  font-size: 14px;
}
</style>
